.fixed {
    position: fixed;
}

/* sidebar */
.bs-sidebar {
    padding-left: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
}

/* all links */
.bs-sidebar .nav>li>a {
    color: #999;
    border-left: 2px solid transparent;
    padding: 4px 20px;
    font-size: 13px;
    font-weight: 400;
}

/* nested links */
.bs-sidebar .nav .nav>li>a {
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 30px;
    font-size: 12px;
}

/* active & hover links */
.bs-sidebar .nav>.active>a, 
.bs-sidebar .nav>li>a:hover, 
.bs-sidebar .nav>li>a:focus {
    color: #563d7c;                 
    text-decoration: none;          
    background-color: transparent;  
    border-left-color: #563d7c; 
}
/* all active links */
.bs-sidebar .nav>.active>a, 
.bs-sidebar .nav>.active:hover>a,
.bs-sidebar .nav>.active:focus>a {
    font-weight: 700;
}
/* nested active links */
.bs-sidebar .nav .nav>.active>a, 
.bs-sidebar .nav .nav>.active:hover>a,
.bs-sidebar .nav .nav>.active:focus>a {
    font-weight: 500;
}

/* hide inactive nested list */
.bs-sidebar .nav ul.nav {
    display: none;           
}
/* show active nested list */
.bs-sidebar .nav>.active>ul.nav {
    display: block;           
}

